body{
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 14px;
  font-family:"SimSun","\5B8B\4F53","Arial Narrow";
}
body>div:nth-of-type(5){
  position: absolute;
  z-index: 9999999;
}
input,button,a {outline:0 none !important;
}
/* ::-webkit-scrollbar{
  display:none;
} */
::-webkit-scrollbar {/**/
            width: 6px;     /**/
            height: 6px;
            border-radius: 27px !important;
}
::-webkit-scrollbar-thumb {/**/
    border-radius: 270px !important;
    /* -webkit-box-shadow: inset 0 0 5px rgba(204, 89, 89, 0.2); */
    background: rgb(155, 155, 155);
}
::-webkit-scrollbar-track {/**/
    /* -webkit-box-shadow: inset 0 0 5px rgba(223, 43, 43, 0.2); */
    border-radius: 0px;
    background: rgba(0, 0, 0, 0.1);
}
